.profile__name {
    font-style: normal;
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    color: #fff;
    margin: 0 0 10px 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media (max-width: 750px) {
    .profile__name {
        font-size: 27px;
        line-height: 33px;
        text-align: center;
        margin: 0 0 8px 0;
    }
}